.menuBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    width:100%;
    height: 44px;
    border-top: 1px solid #dbdbdb;
    background-color: #fff;
}

.postCollection {
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    width:100%;
    height: 44px;
    border-top: 1px solid #dbdbdb;
    background-color: #fff;
}

.posts{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 5px;
}